
.cultivation-status {
  width: 100%;
  margin: 0 auto;
  font-family: 'Microsoft YaHei', 'PingFang SC', sans-serif;
  background: var(--bg-primary);
  border: 1px solid var(--border-color);
  border-radius: 12px;
  overflow: hidden;
  color: var(--text-primary);
  font-size: 14px;
  transition: all 0.3s ease;
  display: flex;
  flex-direction: column;
  position: relative;

  // 背景图案层 - 优化祥云排布
  .pattern-overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-image: var(--pattern-overlay);
    // 祥云图案尺寸和位置优化
    background-size: 100px 65px, 85px 55px, 110px 70px, 90px 60px, 75px 50px;
    background-position:
      5% 15%, // 左上
      85% 25%, // 右上
      15% 65%, // 左下
      75% 70%, // 右下
      45% 85%; // 底部中央
    background-repeat: no-repeat;
    pointer-events: none;
    opacity: 0.25;
    z-index: 0;
  }

  // 仙鹤装饰 - 移到顶部中央
  .crane-decoration {
    position: absolute;
    top: 22px;
    left: 50%;
    transform: translateX(-50%);
    width: 60px;
    height: 60px;
    z-index: 3;
    pointer-events: none;
    animation: crane-float 4s ease-in-out infinite;

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
      filter: drop-shadow(0 3px 12px rgba(0, 0, 0, 0.4));
    }
  }

  @keyframes crane-float {

    0%,
    100% {
      transform: translateX(-50%) translateY(0);
    }

    50% {
      transform: translateX(-50%) translateY(-8px);
    }
  }

  // 确保所有内容在图案层之上
  >* {
    position: relative;
    z-index: 1;
  }

  // 折叠状态
  &.collapsed {

    .tab-nav,
    .content {
      display: none;
    }

    .header {
      border-bottom: none;
    }
  }
}

// 头部
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 16px 12px 16px;
  min-height: 80px;
  background: var(--header-bg);
  border-bottom: 1px solid var(--border-color);

  .header-left {
    display: flex;
    gap: 8px;
    align-items: center;
  }

  .collapse-btn,
  .theme-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 28px;
    height: 28px;
    background: var(--button-bg);
    border: 1px solid var(--border-color);
    border-radius: 6px;
    color: var(--text-accent);
    cursor: pointer;
    transition: all 0.2s;

    &:hover {
      background: var(--button-hover);
      color: var(--text-primary);
      border-color: var(--border-active);
    }

    i {
      font-size: 12px;
    }
  }

  .world-time {
    display: flex;
    align-items: center;
    gap: 6px;
    color: var(--text-accent);

    i {
      color: var(--accent-color);
    }
  }

  .location {
    display: flex;
    align-items: center;
    gap: 8px;
    margin-left: auto;

    .danger-badge {
      font-size: 10px;
      padding: 2px 6px;
      border-radius: 10px;
      color: #fff;
    }
  }
}

// 主题选择器
.theme-selector-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.theme-selector {
  background: var(--bg-primary);
  border: 2px solid var(--border-active);
  border-radius: 12px;
  width: 90%;
  max-width: 500px;
  max-height: 80vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.5);

  .theme-selector-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 16px 20px;
    background: var(--header-bg);
    border-bottom: 1px solid var(--border-color);

    h3 {
      margin: 0;
      font-size: 16px;
      color: var(--text-primary);
      display: flex;
      align-items: center;
      gap: 8px;

      i {
        color: var(--accent-color);
      }
    }

    .close-btn {
      width: 28px;
      height: 28px;
      display: flex;
      align-items: center;
      justify-content: center;
      background: transparent;
      border: 1px solid var(--border-color);
      border-radius: 6px;
      color: var(--text-secondary);
      cursor: pointer;
      transition: all 0.2s;

      &:hover {
        background: var(--button-hover);
        color: var(--text-primary);
        border-color: var(--border-active);
      }
    }
  }

  .theme-list {
    flex: 1;
    overflow-y: auto;
    padding: 12px;

    &::-webkit-scrollbar {
      width: 6px;
    }

    &::-webkit-scrollbar-track {
      background: var(--bg-secondary);
      border-radius: 3px;
    }

    &::-webkit-scrollbar-thumb {
      background: var(--button-bg);
      border-radius: 3px;

      &:hover {
        background: var(--button-hover);
      }
    }
  }

  .theme-item {
    display: flex;
    align-items: center;
    gap: 12px;
    padding: 12px;
    margin-bottom: 8px;
    background: var(--bg-secondary);
    border: 2px solid transparent;
    border-radius: 8px;
    cursor: pointer;
    transition: all 0.2s;
    position: relative;

    &:hover {
      background: var(--button-bg);
      border-color: var(--border-color);
    }

    &.active {
      border-color: var(--border-active);
      background: var(--button-active);
    }

    .theme-preview {
      width: 60px;
      height: 60px;
      border-radius: 8px;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-shrink: 0;

      .theme-preview-border {
        width: 40px;
        height: 40px;
        border: 2px solid;
        border-radius: 6px;
      }
    }

    .theme-info {
      flex: 1;

      .theme-name {
        font-size: 14px;
        font-weight: bold;
        color: var(--text-primary);
        margin-bottom: 4px;
      }

      .theme-desc {
        font-size: 12px;
        color: var(--text-secondary);
      }
    }

    .theme-check {
      color: var(--accent-color);
      font-size: 18px;
    }
  }
}

.theme-selector-fade-enter-active,
.theme-selector-fade-leave-active {
  transition: opacity 0.3s;
}

.theme-selector-fade-enter-from,
.theme-selector-fade-leave-to {
  opacity: 0;
}

// 标签导航
.tab-nav {
  display: flex;
  padding: 8px 12px;
  gap: 4px;
  background: var(--tab-bg);
  border-bottom: 1px solid var(--border-color);
  overflow-x: auto;

  &::-webkit-scrollbar {
    display: none;
  }

  .tab-btn {
    flex: 1;
    min-width: 60px;
    padding: 8px 12px;
    background: transparent;
    border: 1px solid transparent;
    border-radius: 6px;
    color: var(--text-secondary);
    font-size: 12px;
    cursor: pointer;
    transition: all 0.2s;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 4px;
    white-space: nowrap;

    &:hover {
      background: var(--button-bg);
      color: var(--text-accent);
    }

    &.active {
      background: var(--tab-active);
      border-color: var(--border-active);
      color: var(--text-primary);
    }
  }
}

// 内容区域
.content {
  flex: 1;
  min-height: 280px;
  max-height: 600px;
  overflow-y: auto;

  &::-webkit-scrollbar {
    width: 6px;
  }

  &::-webkit-scrollbar-track {
    background: var(--bg-secondary);
    border-radius: 3px;
  }

  &::-webkit-scrollbar-thumb {
    background: var(--button-bg);
    border-radius: 3px;

    &:hover {
      background: var(--button-hover);
    }
  }
}

.panel {
  padding: 16px;
}

// 修炼面板
.cultivation-panel {
  .identity-section {
    text-align: center;
    margin-bottom: 16px;

    .identity-name {
      font-size: 20px;
      font-weight: bold;
      color: var(--text-primary);
      text-shadow: 0 0 10px var(--accent-glow);
    }

    .identity-info {
      color: var(--text-secondary);
      font-size: 12px;
      margin-top: 4px;

      .divider {
        margin: 0 6px;
        opacity: 0.5;
      }
    }
  }

  .realm-section {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 12px;
    margin-bottom: 16px;

    .realm-title {
      font-size: 18px;
      font-weight: bold;
      display: flex;
      align-items: center;
      gap: 6px;

      i {
        animation: spin 10s linear infinite;
      }
    }

    .realm-status {
      font-size: 12px;
      padding: 3px 8px;
      background: var(--button-bg);
      border: 1px solid var(--border-color);
      border-radius: 4px;
      color: var(--accent-color);
    }
  }

  .progress-section {
    margin-bottom: 20px;

    .progress-label {
      display: flex;
      justify-content: space-between;
      font-size: 12px;
      color: var(--text-secondary);
      margin-bottom: 6px;
    }

    .progress-bar {
      height: 8px;
      background: var(--progress-bg);
      border-radius: 4px;
      overflow: hidden;

      .progress-fill {
        height: 100%;
        border-radius: 4px;
        transition: width 0.5s ease;
        box-shadow: 0 0 10px currentColor;
      }
    }

    .progress-percent {
      text-align: right;
      font-size: 11px;
      color: var(--text-secondary);
      margin-top: 4px;
    }
  }

  .stats-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 12px;

    .stat-item {
      text-align: center;
      padding: 10px 8px;
      background: var(--bg-secondary);
      border: 1px solid var(--border-color);
      border-radius: 8px;

      .stat-label {
        font-size: 11px;
        color: var(--text-secondary);
        margin-bottom: 4px;
      }

      .stat-value {
        font-size: 13px;
        font-weight: 500;
        color: var(--text-primary);

        &.gold {
          color: #ffd700;
        }
      }
    }
  }
}

// 神通面板
.skills-panel {
  .skills-list {
    display: flex;
    flex-direction: column;
    gap: 12px;
  }

  .skill-item {
    padding: 12px;
    background: var(--bg-secondary);
    border: 1px solid var(--border-color);
    border-radius: 8px;

    .skill-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 6px;
    }

    .skill-name {
      font-size: 14px;
      font-weight: bold;
      color: var(--accent-color);
    }

    .skill-rank {
      font-size: 11px;
      color: #ffc864;
      padding: 2px 8px;
      background: rgba(255, 200, 100, 0.1);
      border-radius: 4px;
    }

    .skill-desc {
      font-size: 12px;
      color: var(--text-secondary);
      line-height: 1.5;
      margin-bottom: 4px;
    }

    .skill-proficiency {
      font-size: 11px;
      color: var(--text-accent);
    }
  }
}

// 储物面板
.inventory-panel {
  .inventory-section {
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  .section-title {
    font-size: 14px;
    font-weight: bold;
    color: var(--text-accent);
    margin-bottom: 10px;
    display: flex;
    align-items: center;
    gap: 6px;

    i {
      color: var(--accent-color);
    }
  }

  .items-grid {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }

  .item-card {
    padding: 10px 12px;
    background: var(--bg-secondary);
    border: 1px solid var(--border-color);
    border-radius: 6px;

    &.treasure {
      border-color: var(--border-active);
      background: var(--button-active);
    }

    .item-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

    .item-name {
      font-weight: 500;
      color: var(--text-primary);
    }

    .item-count {
      font-size: 12px;
      color: var(--accent-color);
    }

    .item-rank {
      font-size: 11px;
      color: #ffc864;
      margin: 4px 0;
    }

    .item-desc {
      font-size: 11px;
      color: var(--text-secondary);
    }
  }
}

// 红颜面板
.companions-panel {
  .companions-list {
    display: flex;
    flex-direction: column;
    gap: 12px;
  }

  .companion-card {
    padding: 12px;
    background: var(--bg-secondary);
    border: 1px solid var(--border-color);
    border-radius: 8px;

    .companion-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 8px;

      .companion-name {
        font-size: 15px;
        font-weight: bold;
        color: #ffb0c0;
      }

      .companion-realm {
        font-size: 12px;
      }
    }

    .companion-relation {
      font-size: 12px;
      color: var(--text-secondary);
      margin-bottom: 8px;

      .relation-value {
        color: var(--text-accent);
        margin-left: 4px;
      }
    }

    .companion-favor {
      display: flex;
      align-items: center;
      gap: 8px;

      .favor-label {
        font-size: 11px;
        color: var(--text-secondary);
        min-width: 42px;
      }

      .favor-bar {
        flex: 1;
        height: 6px;
        background: var(--progress-bg);
        border-radius: 3px;
        overflow: hidden;

        .favor-fill {
          height: 100%;
          border-radius: 3px;
          transition: width 0.3s ease;
        }
      }

      .favor-value {
        font-size: 12px;
        color: var(--text-accent);
        min-width: 30px;
        text-align: right;
      }
    }
  }
}

// 地图面板
.map-panel {
  .current-location {
    text-align: center;
    padding: 20px;
    background: var(--bg-secondary);
    border: 1px solid var(--border-color);
    border-radius: 10px;
    margin-bottom: 16px;

    .location-name {
      font-size: 22px;
      font-weight: bold;
      color: #a0d0ff;
      margin-bottom: 4px;
    }

    .location-layer {
      font-size: 12px;
      color: var(--accent-color);
      margin-bottom: 10px;
    }

    .location-desc {
      font-size: 13px;
      color: var(--text-secondary);
      line-height: 1.5;
    }
  }

  .connections {
    margin-bottom: 20px;

    .connections-title {
      font-size: 13px;
      color: var(--text-secondary);
      margin-bottom: 10px;
    }

    .connections-list {
      display: flex;
      flex-wrap: wrap;
      gap: 8px;

      .connection-item {
        padding: 6px 12px;
        background: var(--bg-secondary);
        border: 1px solid;
        border-radius: 15px;
        font-size: 12px;
        color: var(--text-accent);
      }
    }
  }

  // 任务列表
  .tasks-section {
    margin-bottom: 20px;

    .section-title {
      font-size: 14px;
      font-weight: bold;
      color: var(--text-accent);
      margin-bottom: 10px;
      display: flex;
      align-items: center;
      gap: 6px;

      i {
        color: var(--accent-color);
      }
    }

    .tasks-list {
      display: flex;
      flex-direction: column;
      gap: 10px;
    }

    .task-item {
      padding: 12px;
      background: var(--bg-secondary);
      border: 1px solid var(--border-color);
      border-radius: 8px;
      border-left: 3px solid;

      &.task-进行中 {
        border-left-color: #4a9eff;
      }

      &.task-已完成 {
        border-left-color: #44aa44;
        opacity: 0.7;
      }

      &.task-已失败 {
        border-left-color: #ff4444;
        opacity: 0.7;
      }

      .task-header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 6px;

        .task-name {
          font-size: 13px;
          font-weight: 500;
          color: var(--text-primary);
        }

        .task-type {
          font-size: 10px;
          padding: 2px 8px;
          border-radius: 10px;
          font-weight: 500;

          &.type-主线 {
            background: rgba(255, 100, 100, 0.2);
            color: #ff6464;
          }

          &.type-支线 {
            background: rgba(100, 150, 255, 0.2);
            color: #6496ff;
          }

          &.type-每日 {
            background: rgba(100, 200, 100, 0.2);
            color: #64c864;
          }

          &.type-临危受命 {
            background: rgba(255, 150, 50, 0.2);
            color: #ff9632;
          }
        }
      }

      .task-target {
        font-size: 12px;
        color: var(--text-secondary);
        margin-bottom: 4px;
        line-height: 1.4;
      }

      .task-status {
        font-size: 11px;
        color: var(--text-accent);
      }
    }
  }

  // 声望系统
  .reputation-section {
    .section-title {
      font-size: 14px;
      font-weight: bold;
      color: var(--text-accent);
      margin-bottom: 10px;
      display: flex;
      align-items: center;
      gap: 6px;

      i {
        color: var(--accent-color);
      }
    }

    .reputation-list {
      display: flex;
      flex-direction: column;
      gap: 10px;
    }

    .reputation-item {
      padding: 10px 12px;
      background: var(--bg-secondary);
      border: 1px solid var(--border-color);
      border-radius: 8px;

      .reputation-faction {
        font-size: 13px;
        font-weight: 500;
        color: var(--text-primary);
        margin-bottom: 6px;
      }

      .reputation-bar {
        height: 6px;
        background: var(--progress-bg);
        border-radius: 3px;
        overflow: hidden;
        margin-bottom: 6px;

        .reputation-fill {
          height: 100%;
          border-radius: 3px;
          transition: width 0.3s ease;
        }
      }

      .reputation-info {
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-size: 11px;

        .reputation-value {
          color: var(--text-accent);
        }

        .reputation-relation {
          color: var(--text-secondary);
        }
      }
    }
  }
}

// 图鉴面板
.gallery-panel {
  padding: 16px !important;
  display: flex;
  flex-direction: column;
  align-items: center;

  .gallery-container {
    display: inline-flex;
    flex-direction: row;
    gap: 20px;
    overflow-x: auto;
    overflow-y: hidden;
    padding-bottom: 10px;
    max-width: 100%;

    &::-webkit-scrollbar {
      height: 8px;
    }

    &::-webkit-scrollbar-track {
      background: var(--bg-secondary);
      border-radius: 4px;
    }

    &::-webkit-scrollbar-thumb {
      background: var(--button-bg);
      border-radius: 4px;

      &:hover {
        background: var(--button-hover);
      }
    }
  }

  .gallery-card {
    flex-shrink: 0;
    width: 280px;
    aspect-ratio: 2 / 3;
    perspective: 1000px;
    cursor: pointer;
  }

  .card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    transition: transform 0.6s ease;
    transform-style: preserve-3d;
    border-radius: 12px;
    box-shadow: 0 8px 30px rgba(0, 0, 0, 0.5);
  }

  .gallery-card.flipped .card-inner {
    transform: rotateY(180deg);
  }

  .card-face {
    position: absolute;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
    border-radius: 12px;
    overflow: hidden;
    border: 2px solid var(--border-color);
  }

  .card-front {
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    .card-name-bar {
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      padding: 12px;
      background: linear-gradient(to top, rgba(0, 0, 0, 0.85), transparent);
      color: #fff;
      font-size: 16px;
      font-weight: bold;
      text-align: center;
      text-shadow: 0 2px 6px rgba(0, 0, 0, 0.9);
    }
  }

  .card-back {
    transform: rotateY(180deg);
    background: linear-gradient(135deg, #1a1a2e 0%, #16213e 100%);

    .back-content {
      width: 100%;
      height: 100%;
      position: relative;

      .back-image {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }

      .back-info-panel {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        padding: 20px 16px;
        background: linear-gradient(to top, rgba(0, 0, 0, 0.9) 0%, rgba(0, 0, 0, 0.7) 50%, transparent 100%);
        text-align: center;

        .back-name {
          font-size: 18px;
          font-weight: bold;
          color: #a0c0ff;
          margin-bottom: 8px;
          text-shadow: 0 0 15px rgba(100, 150, 255, 0.6), 0 2px 4px rgba(0, 0, 0, 0.8);
        }

        .back-text {
          font-size: 14px;
          color: #d0e0f0;
          line-height: 1.6;
          font-style: italic;
          text-shadow: 0 1px 3px rgba(0, 0, 0, 0.8);
        }
      }
    }
  }

  .gallery-card::after {
    content: '点击翻转';
    position: absolute;
    top: 10px;
    right: 10px;
    padding: 4px 10px;
    background: rgba(0, 0, 0, 0.6);
    color: #a0b8d0;
    font-size: 11px;
    border-radius: 12px;
    opacity: 0;
    transition: opacity 0.3s;
    pointer-events: none;
    z-index: 10;
  }

  .gallery-card:hover::after {
    opacity: 1;
  }

  .gallery-card.flipped::after {
    content: '点击翻回';
  }
}

// 空状态提示
.empty-hint {
  text-align: center;
  padding: 20px;
  color: #506070;
  font-style: italic;
}

// 底部
.footer {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 10px 16px;
  background: var(--footer-bg);
  border-top: 1px solid var(--border-color);

  .footer-item {
    display: flex;
    align-items: center;
    gap: 6px;
    font-size: 12px;
    color: var(--text-secondary);

    i {
      color: var(--accent-color);
    }
  }

  .footer-center {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 6px;
    color: var(--text-accent);

    .moon-twinkle {
      animation: moon-twinkle 3s ease-in-out infinite;
    }

    .star-icon {
      font-size: 16px;
      color: var(--accent-color);
      animation: spin 3s linear infinite;
      opacity: 0.7;
      transition: all 0.3s ease;

      &:hover {
        opacity: 1;
        transform: scale(1.2);
      }
    }
  }
}

// 动画
@keyframes spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes moon-twinkle {
  0%, 100% {
    opacity: 1;
    filter: drop-shadow(0 0 2px var(--accent-color));
  }
  
  50% {
    opacity: 0.4;
    filter: drop-shadow(0 0 8px var(--accent-color));
  }
}
